* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
}

.App {
  height: 100vh;
  padding-top: 10px;
  background-color: #1b1b32;
  font-family: Lato, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.freecodecamp-logo-contenedor {
  width: 100%;
  height: 50px;
  margin: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.freecodeCamp-logo {
  height: 40px;
}

section {
  width: 400px;
  height: 600px;
  padding: 14px;
  background-color: #99c9ff;
  border-radius: 15px;
  border: 4px solid #222;
  box-shadow: 0px 0px 1px 1px inset rgb(0, 0, 0);
}

.fila {
  margin: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.boton-contenedor {
  height: 83px;
  width: 303px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 38px;
  background-color: #1b1b32;
  color: #fff;
  border-radius: 11px;
  border: 2px solid;
  margin: 4px;
  cursor: pointer;
  user-select: none;
}

.operador {
  background-color: #00471b;
}

.boton-contenedor:active {
  transform: scale(.9);
}

.input {
  height: 75px;
  border-radius: 10px;
  margin-bottom: 20px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-weight: bold;
  font-size: 30px;
  background-color: #0a0a23;
  color: #fff;
  padding: 11px 30px 11px 11px;
  border: 1px solid rgb(158, 157, 157);
  box-shadow: 0px 0px 1px 1px inset #ccc;
  overflow: auto;
}

.boton-clear {
  font-size: 28px;
}

.alerta {
  width: 50%;
  background-color: #fff;
  text-align: center;
  justify-content: center;
  padding: 10px;
  position: absolute;
  top: 10px;
  display: none;
  animation: alerta .3s;
}

@keyframes alerta {
  from {
    top: -10px;
  }

  to {
    top: 0px;
  }
}